body {
    font-family: sans-serif;
    background: cadetblue;
}

.App {
    max-width: 950px;
    /*margin: 0 auto;*/
    margin: 10px;
}

.App h1 {
    font-size: 3rem;
    margin: 4rem 0 0 0;
}

.App p {
    color: #1a1a11;
    font-size: 1.5rem;
}

.input-wrapper {
    position: relative;
}

.input-wrapper label {
    display: inline-block;
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 4px;
    background: coral;
    color: #fff;
}

.input-wrapper input[type="file"] {
    position: absolute;
    left: 26px;
    top: 6px;
    z-index: -1;
    font-size: 15px;
    color: #1a1a11;
}

.result {
    margin-top: 4rem;
    display: grid;
    align-items: center;
    justify-content: center;
}

.result .box-image {
    background: coral;
    height: 100%;
    padding: 1rem;
}

.box-p {
    margin: 20px;
    background: #ddd;
    height: 100%;
    padding: 1rem;
}

.result img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.result p {
    line-height: 1.5rem;
    font-size: 1rem;
}
.Button {
    margin-bottom: 10px;
    border-radius: 10%;
    font-weight: bold;
}

@media (min-width: 1000px) {
    .result {
        grid-template-columns: 1fr 1fr;
    }
}
